import React, {Component} from "react";
import styled from "styled-components";
import DevIcon from "devicon-react-svg";
import Tooltip from "./Tooltip";

const StyledCard = styled.div`
	align-items: flex-start;
	display: flex;
	flex-direction: column;
	margin: 25px;
	padding: 25px;
	border-radius: 1em;
	background-color: ${props => props.theme.cardBackgroundColor};
	background-image: url("${props => props.theme.cardBackgroundImage}");
	font-family: ${props => props.theme.cardBodyFont};
	color: ${props => props.theme.cardBodyColor};

	.project-title {
		display: block;
		font-family: ${props => props.theme.cardTitleFont};
		color: ${props => props.theme.cardTitleColor};
	}

	.project-image {
		display: block;
	}

	.project-description {
		font-family: ${props => props.theme.cardBodyFont};
		margin: 0;
	}

	.project-links {
		list-style: none;
		padding: 0;
		color: ${props => props.theme.cardBodyColor};
	}

	li.project-link > a{
		display: inline-flex;
		flex-direction: row-reverse;
		align-items: center;
	}

	li.project-link > a:hover {
		span {
			color : ${props => props.theme.cardBodyHoverColor};
		}
	}

	span.link-caption {
	}

	.dev-icons-list {
		margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content: left;
        width: 100%;
	}

	/* to style LinkIcon when icon name is not found by component*/
	span[class*="icon-not-found"] {
		display: inline-block;
		padding: 0.5em;
		color: ${props => props.theme.techIconFillColor}
	}
	span[class*="icon-not-found"]:hover {
		color: ${props => props.theme.techIconHoverColor};
		transition: 0.6s;
	}
`;

const LinkIcon = styled(DevIcon)`
	fill: ${props => props.theme.techIconFillColor};
	width: 3.5em;
	height: auto;
	text-align: end;
	align-content: flex-start;
	padding-left: 0.75em;
	&:hover {
		fill: ${props => props.theme.techIconHoverColor};
		transition: 0.6s;
	}
`;

const StyledIcon = styled(DevIcon)`
	fill: ${props => props.theme.techIconFillColor};
	width: 3em;
	height: 3em;
	&:hover {
		fill: ${props => props.theme.techIconHoverColor};
		transition: 0.6s;
	}
`;

const TechIcon = Tooltip(StyledIcon);

class ProjectCard extends Component {
	constructor (props) {
		super(props);
		this.state = {...props};
	}
	render() {
		return (
			<StyledCard className={`project-card project-${this.state.title}`}>
				<h2 className="project-title">{this.state.title}</h2>
				{this.state.imageUrl ? 
				(<img className="project-image" src={this.state.imageUrl} alt={this.state.title}/>) : null
				}
				<p className="project-description">{this.state.description}</p> 
				<ul className="project-links">
					{this.state.links.map((link, i) => {
						return (
							<li className="project-link" key={i}>
								<a href={link.linkUrl}>
								{
									link.linkIcon ? (
										<LinkIcon icon={link.linkIcon} />
									) : null
								}
								<span className="link-caption">{link.linkTitle}</span>
								</a>
							</li>
						);
					})}
				</ul>
				{this.state.devIcons.length ? 
					(
					<ul className="dev-icons-list">
						{this.state.devIcons.map((icon, i) => (
						<li className="dev-icon-list-item" key={i}>
							<TechIcon icon={icon} className="dev-icon" tooltip={icon}/>
						</li>
						))}
					</ul>
					) : null
				}
			</ StyledCard>
		);
	}
}

export default ProjectCard;
